import {tasksApi} from '../../api/todosApi'

const SET_TASKSLIST = 'tasksReducer/SET_TASKSLIST';
const ADD_TASK = 'tasksReducer/ADD_TASK';
const DEL_TASK = 'tasksReducer/DEL_TASK';

const initialState = {
  tasks: []
}


const todosReducer = (state = initialState, action) => {
  switch (action.type) {
    case SET_TASKSLIST:
      return { ...state, tasks: action.tasksList }
    case ADD_TASK:
      let newTask = {
        title: action.title
      }
      return { ...state, tasks: [...state.tasks, newTask] }
    case DEL_TASK:
      return { ...state, tasks: action.tasksList }
    default:
      return state;
  }

}

export const getTasksListSuccess = (tasksList) => ({ type: SET_TASKSLIST, tasksList: tasksList });
export const setTaskSuccess = (title) => ({ type: ADD_TASK, title: title })


export const setTasksList = () => async (dispatch) => {
  let response = await tasksApi.getTasks()
  dispatch(getTasksListSuccess(response))
}

export const setTask = (title, todoId) => async (dispatch) => {
  dispatch(setTaskSuccess(title))
  let response = await tasksApi.addTask(title,todoId)
  dispatch(setTasksList())
}

export const delTask = (taskId) => async (dispatch) => {
  let response = await tasksApi.delTask(taskId)
  dispatch(setTasksList())

}

export const changeTask = (task) => async(dispatch)=>{
      let response  = await tasksApi.changeTask(task)
      dispatch(setTasksList())
}




export default todosReducer;
