import * as axios from 'axios';


const instance = axios.create({
    baseURL: 'https://quiet-eyrie-76238.herokuapp.com/'
})

export const todosApi = {   
    async getTodos() {
        let response = await instance.get(`todos`)
        return response.data
    },
    async addTodo(title) {
        let response = await instance.post(`todos`, {title: title})
        return response.data
    },
    async delTodo(todoId) {
        let response = await instance.delete(`todos/${todoId}`)
        return response.data
    }
}

export const tasksApi = {
    async getTasks(){
        let response = await instance.get(`tasks`)
        return response.data
    },
    async addTask(title, todoId) {
        let response = await instance.post(`tasks`, {title: title, todoId:todoId})
        return response.data
    },
    async delTask (taskId){
        let response = await instance.delete(`tasks/${taskId}`)

    },
    async changeTask(task) {
        let response = await instance.put(`tasks/`, {task: task})
        return response.data
    }

}


